* {
  margin: 0;
  padding: 0;
}

.invisible {
  opacity: 0;
}

.pageWrapper {
  width: 100%;
  text-align: center;
  background: white;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.loginWrapper {
  width: 100%;
  text-align: center;
  padding-top: 55px;
}

.loginWrapper input {
  outline: none;
  padding-left: 5px;
}

.entry {
  font-size: 13px;
  color: grey;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: bold;
}

.inputModule {
  text-align: start;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 35px;
}

.input {
  font-size: 15px;
  height: 50px;
  width: 270px;
  text-align: center;
  vertical-align: bottom;
  border: none;
  border-bottom: 1px solid grey;
  margin-top: 20px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: bold;
  background: transparent;
}

.error {
  padding-top: 10px;
  color: red;
  font-size: 15px;
  font-weight: bold;
  line-height: '20px';
  font-family: Verdana, Geneva, Tahoma, sans-serif;

}

.button {
  margin-top: 20px;
  height: 40px;
  width: 175px;
  border-radius: 20px;
  color: white;
  background: rgb(252, 132, 44);
  font-size: 14px;
  box-shadow: 0 5px 10px rgb(252, 132, 44);
  border: none;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: bold;
  cursor: pointer;
}

.button:focus {
  outline: none;
}

.butModule {
  display: flex;
  align-items: center;
  flex-direction: column;
}

@media(min-width: 767px) {
  .pageWrapper {
    width: 100%;
    text-align: center;
    background-repeat: no-repeat;
    background-position: bottom right;
    height: 100vh;
    display: flex;
    align-items: flex-start;
    flex-direction: column;

  }

  .loginWrapper {
    width: 100%;
    text-align: start;
    display: flex;
    flex-direction: column;
    padding-left: 90px;
    padding-top: 60px;
    background-image: url(../../assets/images/tablet-bg-register.jpg);
    background-repeat: no-repeat;
    background-position: bottom right;
    height: 100vh
  }

  .inputModule {
    text-align: start;
    display: flex;
    align-items: flex-start;

  }

  .input {
    font-size: 15px;
    height: 50px;
    width: 220px;
    text-align: start;
    vertical-align: bottom;
    border: none;
    border-bottom: 1px solid grey;
    margin-top: 20px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
    background: none;
    align-items: flex-start;
  }

  .butModule {
    margin-top: 45px;
    display: flex;
    align-items: flex-start;
    flex-direction: column;

  }


  .entry {
    font-size: 13px;
    padding-bottom: 35px;
    color: grey;
    text-align: start;
    font-weight: bold;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
  }
}

@media(min-width: 1024px) {

  .pageWrapper {
    width: 100%;
    text-align: center;
    background-image: url(../../assets/images/desktop-bg-REGISTER.jpg);
    background-repeat: no-repeat;
    background-position: bottom right;
    background-size: cover;
    height: 847px;
    height: 100vh;
  }

  .input {
    font-size: 15px;
    height: 50px;
    width: 345px;
    text-align: start;
    vertical-align: bottom;
    border: none;
    border-bottom: 1px solid grey;
    margin-top: 20px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
    background: none;
  }

  .loginWrapper {
    text-align: start;
    padding-left: 115px;
    padding-top: 10px;
    background-image: none;
  }

  .butModule {
    margin-top: 45px;
    text-align: start;
  }


  .entry {
    font-size: 13px;
    padding-bottom: 35px;
    color: grey;
    text-align: start;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
  }
}
